// Import Toastr CSS
@import "~toastr/build/toastr.min.css";


/* Custom styles for the advice pricing form */
#advice-lab-pricing-container {

    .overlay {
        position: absolute;
        top: 35%;
        left: 0;
        margin: auto;
        right: 0;
        display: none;
    }

    .loader {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 60px;
        height: 60px;
        border-radius: 50%;
        border: 4px solid #009ed2;
        border-right-color: transparent;
        transform-origin: center;
        animation: rotate 1s linear infinite;
    }

    @keyframes rotate {
        from {
            transform: rotate(0);
        }

        to {
            transform: rotate(360deg);
        }
    }

    .wp-block-spacer {
        display: none;
    }

    .form-wrap {
        background: rgba(255, 255, 255, 1);
        width: 100%;
        padding: 75px 75px 225px 75px;
        margin: 0 auto;
        position: relative;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        border-radius: 10px;
        -webkit-box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.15);
        -moz-box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.15);
        box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.15);
    }

    .form-wrap:before {
        content: "";
        width: 90%;
        height: calc(100% + 30px);
        left: 0;
        right: 0;
        margin: 0 auto;
        position: absolute;
        top: -30px;
        background: #101685;
        z-index: -1;
        opacity: 0.8;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        border-radius: 10px;
        -webkit-box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.15);
        -moz-box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.15);
        box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.15);
    }

    .form-group {
        margin-bottom: 25px;
    }

    .form-group>label {
        display: block;
        font-size: 20px;
        line-height: 27px;
        color: #000;
        margin-bottom: 10px;
    }

    .custom-control-label {
        color: #000;
        font-size: 16px;
    }

    .form-control {
        height: 50px;
        background: #ecf0f4;
        border-color: transparent;
        padding: 0 15px;
        font-size: 16px;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    }

    select.form-control {
        margin-top: 10px;
    }

    .form-control:focus {
        border-color: #007BFF;
        -webkit-box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
        -moz-box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
        box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
    }

    textarea.form-control {
        height: 100px;
        padding-top: 15px;
        resize: none;
    }

    .btn {
        padding: .657rem .75rem;
        font-size: 18px;
        letter-spacing: 0.050em;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    }

    .btn-primary {
        color: #fff;
        background-color: #3208F4;
        border-color: #3208F4;
        font-size: 16px;
        padding: 12px 24px !important;
    }

    // .btn-primary:hover {
    //     color: #101685;
    //     background-color: #ffffff;
    //     border-color: #101685;
    //     -webkit-box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
    //     -moz-box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
    //     box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
    // }

    .btn-primary:focus,
    .btn-primary.focus,
    .btn-primary:active {
        color: #fff;
    }

    .btn-primary:not(:disabled):not(.disabled):active,
    .btn-primary:not(:disabled):not(.disabled).active,
    .show>.btn-primary.dropdown-toggle {
        color: #101685;
        background-color: #ffffff;
        border-color: #101685;
    }

    .btn-primary:not(:disabled):not(.disabled):active:focus,
    .btn-primary:not(:disabled):not(.disabled).active:focus,
    .show>.btn-primary.dropdown-toggle:focus {
        -webkit-box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
        -moz-box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
        box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
    }

    @media (min-width: 768px) {
        .col-md-6 {
            flex: 0 0 auto;
            width: 50%;
            max-width: 50%;
        }
    }

    /* Grid container */



    .grid-container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        /* Three columns by default */
        gap: 0 35px;
        /* Gap between grid items */
    }

    .grid-container-2 {
        display: grid;
        grid-template-columns: 2fr 1fr;
        /* Two columns, first takes 2/3 and second takes 1/3 of the width */
        /* two columns by default */
        gap: 0 35px;
        /* Gap between grid items */
    }

    /* Grid items */
    .grid-item {
        box-sizing: border-box;
    }

    /* Responsive adjustments */
    @media (max-width: 992px) {
        .grid-container {
            grid-template-columns: repeat(2, 1fr);
            /* Two columns on tablets */
        }

    }

    @media (max-width: 768px) {
        .grid-container {
            grid-template-columns: 1fr;
            /* One column on mobile */
        }

        .grid-container-2 {
            grid-template-columns: 1fr;
            /* One column on mobile */
        }

    }


    .row {
        display: flex;
        flex-wrap: wrap;
        margin-right: -15px;
        margin-left: -15px;
    }

    .col-md-6 {
        padding-right: 15px;
        padding-left: 15px;
    }

    .mb-0 {
        margin-bottom: 0;
    }

    .pb-0 {
        padding-bottom: 0;
    }

    .custom-control.custom-radio.custom-control-inline {
        display: inline-block;
        margin-right: 15px;
        line-height: 24px;
    }

    .custom-control.custom-radio.custom-control-inline .custom-control-input {
        margin-left: 0;
    }

    .custom-control.custom-checkbox.custom-control-inline {
        line-height: 24px;
        display: flex;
        align-items: flex-start;
        gap: 8px;
    }

    .custom-control.custom-checkbox.custom-control-inline .custom-control-input {
        height: 24px;
        margin: 0;
    }

    .flex-8 {
        display: flex;
        flex-direction: column;
        gap: 8px;
    }

    .small,
    small{
        font-size: 80%;
        font-weight: bold;
        color: #101685;
    }
    .custom-label {
        font-size: 80%;
        font-weight: 700;
        color: #101685;
        line-height: 26px;
        margin-bottom: 5px;
    }


    .result-wrapper {
        background-color: #101685;
        color: #fff;
        position: absolute;
        right: 0;
        bottom: 50px;
        padding: 35px 35px 35px 50px;
        border-top-left-radius: 110px;
        border-bottom-left-radius: 110px;
    }

    #advice-lab-pricing-result {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #advice-lab-pricing-result label {
        font-size: 26px;
        line-height: 28px;
        padding-bottom: 15px;
        font-weight: bold !important;
    }

    #advice-lab-pricing-result .category {
        font-size: 24px;
        line-height: 44px;
        font-weight: bold !important;
    }

    #advice-lab-pricing-result .price span {
        font-size: 65px;
        font-weight: bold !important;
        display: inline-block;
        line-height: 65px;
    }

    #advice-lab-pricing-result .price {
        font-size: 20px;
        line-height: 28px;
        font-weight: bold !important;
    }
    
    .grid-submit {
        text-align: center;
    }

    /* Hide element on all screens */
    .d-none {
        display: none !important;
    }

    /* Hide element on small screens and below */
    @media (max-width: 576px) {
        .d-sm-none {
            display: none !important;
        }
    }

    /* Hide element on medium screens and below */
    @media (max-width: 768px) {
        .d-md-none {
            display: none !important;
        }
    }

    /* Hide element on large screens and below */
    @media (max-width: 992px) {
        .d-lg-none {
            display: none !important;
        }
    }

    /* Hide element on extra-large screens and below */
    @media (max-width: 1200px) {
        .d-xl-none {
            display: none !important;
        }
    }

    /* Hide element on extra-extra-large screens and below (Bootstrap 5.1+) */
    @media (max-width: 1400px) {
        .d-xxl-none {
            display: none !important;
        }
    }

    /* Show element as a block-level element */
    .d-block {
        display: block !important;
    }

    /* Show element as a block-level element on small screens and up */
    @media (max-width: 576px) {
        .d-sm-block {
            display: block !important;
        }
    }

    /* Show element as a block-level element on medium screens and up */
    @media (max-width: 768px) {
        .d-md-block {
            display: block !important;
        }
    }

    /* Show element as a block-level element on large screens and up */
    @media (max-width: 992px) {
        .d-lg-block {
            display: block !important;
        }
    }

    /* Show element as a block-level element on extra-large screens and up */
    @media (max-width: 1200px) {
        .d-xl-block {
            display: block !important;
        }
    }

    /* Show element as a block-level element on extra-extra-large screens and up (Bootstrap 5.1+) */
    @media (max-width: 1400px) {
        .d-xxl-block {
            display: block !important;
        }
    }
    @media (max-width: 1200px) {
        .form-wrap {
            padding: 75px 75px 285px 75px;
        }
    }

    @media (max-width: 992px) {
        .grid-submit {
            text-align: left;
        }
    }

    @media (max-width: 768px) {
        .form-wrap {
            padding: 30px;
        }
        .small,small {
            font-size: 18px;
            line-height: 21px;
        }
        
        .form-group p {
            font-size: 16px;
            line-height: 27px;
        }
        
        .form-group:not(.flex-8) {
            margin-bottom: 20px;
        }

        .result-wrapper {
            position: relative;
            right: 0;
            bottom: 0;
            padding: 35px 35px 35px 50px;
            border-top-left-radius: 110px;
            border-bottom-left-radius: 110px;
            display: inline-block;
            margin-top: 35px;
            margin-right: -30px;
        }

    }

    @media (max-width: 576px) {
        .result-wrapper {
            padding: 30px 30px 30px 35px;
            margin-left: -25px;
        }

        #advice-lab-pricing-result label {
            font-size: 26px;
            line-height: 28px;
        }

        #advice-lab-pricing-result .category {
            font-size: 24px;
            line-height: 28px;
        }

        #advice-lab-pricing-result .price {
            font-size: 16px;
            line-height: 26px;
        }

        #advice-lab-pricing-result .price span {
            font-size: 40px;
            line-height: 48px;
        }


    }
    @media (max-width: 360px) {
        .result-wrapper {
            padding: 30px 15px 30px 30px;
            margin-left: -25px;
        }
        #advice-lab-pricing-result .price span {
            font-size: 35px;
            line-height: 45px;
        }
    }

    .text-right {
        text-align: right;
    }

    .mb-25 {
        margin-bottom: 25px;
    }

}

#toast-container>div {
    opacity: 1 !important;
}